body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.page-container{
  height: 100%;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}
.header{
  background: #366db3;
  padding: 15px;
}
.header .logo-container h2{
  margin: 0;
  color: #fff;
  font-size: 26px;
  line-height: 32px;
}
.page-content{
  height: 100%;
  padding: 20px;
}
.d-flex{
  display: -ms-flexbox;
  display: flex;
}
.align-items-center{
  -ms-flex-align: center;
      align-items: center;
}
.justify-content-between{
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.justify-content-center{
  -ms-flex-pack: center;
      justify-content: center;
}
.flex1{
  -ms-flex: 1 1;
      flex: 1 1;
}
.flex50{
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
  width: 50%;
}
.render-container{
  padding: 10px;
  height: 100%;
  white-space: normal;
  word-break: break-word;
}
.config-container{
  padding: 10px 25px;
  border-left: 1px solid #ddd;
}
.title-container{
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
}
.config-container h3{
  margin-bottom: 5px;
  margin-top: 0;
}
.config-container p{
  margin-bottom: 10px;
  margin-top: 0;
  font-size: 13px;
}
.form-group{
  margin-bottom: 10px;
}
.form-group:last-child{
  margin-bottom: 0;
}
.form-group label{
  display: block;
  font-size: 13px;
  margin-bottom: 5px;
}
.form-group .form-control{
  border-radius: 4px;
  border:1px solid #ddd;
  font-size: 15px;
  width: 70%;
  max-width: 70%;
  line-height: 20px;
  outline: none;
  padding: 8px;
}
.form-group select.form-control{
  width: 75%;
  max-width: 75%;
}
.footer{
  background: #ccc;
}
.footer .copyright{
  margin: 0;
  padding: 6px;
  text-align: center;
  font-size: 13px;
  color: #333;
}



